import React, { Component } from 'react';
 
class Header extends Component {
  render() {
    const {name}=this.props
    const {borrower}=this.props
    return (
      <div>
       <h1>Books written by {name}, 
       borrowed by {borrower}</h1>

      </div>
    );
    
  }
}
 
export default Header;
